.container {
  padding: 0 20px;
}
@media (min-width: 1200px) {
  .container {
    padding: 0;
    width: 1128px;
    margin: 0 auto;
  }
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.button {
  padding: 0;
  margin: 0;
  background: none;
  border: none;
  cursor: pointer;
  text-align: center;
  margin: 0 auto;
  display: block;
  border: 3px solid #ffffff;
  padding: 15px 35px;
  border-radius: 29px;
  font-weight: bold;
}
.body {
  margin: 0;
  font-size: 18px;
  font-family: "Open Sans";
  display: flex;
  flex-direction: column;
  min-height: 100%;
  position: relative;
}
.body__main-header {
  position: absolute;
  z-index: 1;
}
.main-header {
  background-color: rgba(29, 38, 49, 0.75);
  padding: 21px 0;
  width: 100%;
}
.main-header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.navigation__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: none;
}
.navigation__item {
  width: 100%;
}
.navigation__link {
  display: block;
  color: #ffffff;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: bold;
}
.burger {
  padding: 0;
  margin: 0;
  background: none;
  border: none;
  cursor: pointer;
}
.big-title {
  margin: 0;
  font-size: 24px;
  text-align: center;
  font-weight: 400;
}
.text {
  margin: 0;
  font-size: 14px;
  line-height: 24px;
}
.banner {
  display: flex;
  flex-direction: column;
}
.banner__title {
  color: #ffffff;
}
.banner__container {
  background: url("../img/back-mobile.jpg") no-repeat;
  min-height: 462px;
  box-sizing: border-box;
  padding-top: 95px;
  background-size: cover;
  background-position: 100% center;
  position: relative;
}
.banner__container::before {
  content: url("../img/phone-image-banner.png");
  position: absolute;
  left: 0;
  bottom: 0;
  display: block;
  width: 320px;
  height: 286px;
  left: 50%;
  transform: translateX(-50%);
}
.banner__bottom {
  background-color: #1D2631;
  padding-top: 28px;
  padding-bottom: 38px;
}
.banner__button {
  color: #ffffff;
  text-decoration: none;
  margin-bottom: 32px;
}
.banner__text {
  color: #ffffff;
  text-align: center;
}
.banner__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  margin-bottom: 28px;
}
.banner__item {
  display: block;
}
.banner__item--apple::before {
  content: url("../img/icons/apple.svg");
  display: block;
  width: 47px;
  height: 56px;
}
.banner__item--android::before {
  content: url("../img/icons/android.svg");
  display: block;
  width: 43px;
  height: 47px;
}
.banner__item--microsoft::before {
  content: url("../img/icons/microsoft.svg");
  display: block;
  width: 44px;
  height: 41px;
}
.benefits {
  padding-top: 46px;
}
.benefits__title {
  margin-bottom: 40px;
}
.benefits__title:last-child {
  margin-bottom: 0;
}
.benefits__header {
  margin-bottom: 41px;
}
.benefits__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  background-color: #f2f2f2;
  overflow: hidden;
  padding-top: 53px;
}
.benefits__list::after {
  content: url(../img/phone-image-example.png);
  width: 157px;
  height: 320px;
  display: block;
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  bottom: -37px;
}
.benefits__advantage-card {
  margin-bottom: 43px;
}
.benefits__advantage-card:last-child {
  margin-bottom: 0;
}
.advantage-card {
  text-align: center;
  padding: 0 20px;
}
.advantage-card__title {
  font-size: 18px;
  line-height: 24px;
  margin: 0;
  margin-bottom: 24px;
}
.reviews {
  padding: 50px 0 60px 0;
}
.reviews__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
.review-card__user {
  font-size: 18px;
  line-height: 30px;
  text-align: center;
}
.review-card__text {
  font-size: 14px;
  line-height: 30px;
  text-align: center;
}
.switches-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  column-gap: 19px;
}
.switches-list__button {
  width: 6px;
  height: 6px;
  margin: 0;
  padding: 0;
  background-color: rgba(29, 38, 49, 0.3);
  border: none;
  border-radius: 50%;
  cursor: pointer;
}
